<template>
    <div class="stage">
        <div :class="`blackscreen ${hideblackscreen ? `hide` : ``}`"></div>
        <div class="model" ref="model"></div>
        <audio autoplay loop>
          <source src="../assets/theatre opening audio.mp3" type="audio/ogg">
          <source src="../assets/theatre opening audio.mp3" type="audio/mpeg">
        </audio>
    </div>
  </template>
  
  <script>
  /* eslint-disable no-unused-vars */
  import * as THREE from "../../node_modules/three/build/three.module.js";
  import { OrbitControls } from "../../node_modules/three/examples/jsm/controls/OrbitControls.js";
  import { GLTFLoader } from "../../node_modules/three/examples/jsm/loaders/GLTFLoader.js";
  import { OBJLoader } from "../../node_modules/three/examples/jsm/loaders/OBJLoader.js";

  import {toRaw} from "vue";
  export default {
    name: "ThreeJS",
    watch: {
      mousemoveObj(e) {
       if (this.camera) {
        this.camera.rotation.y = -(e.move.x - (window.innerWidth / 2)) / 1000;
        this.camera.rotation.x = -(e.move.y - (window.innerHeight / 2)) / 1000;
       }
      },
      clickObj: {
        handler(e) {
          // If you go back, delete all instances of this stage
          if (e.target.className === "goback" && this.scene) {
            this.scene.remove(this.pointlight);
            this.scene.remove(this.gltfscene);
            this.doAnimate = false;
            cancelAnimationFrame(this.animate);
          }
        }
      },
      resizeObj: {
        handler() {
          if (this.camera && this.renderer) {
            this.camera.aspect = window.innerWidth / window.innerHeight;
            this.camera.updateProjectionMatrix();
            this.renderer.setSize(window.innerWidth, window.innerHeight);
          }
        },
        deep: true,
        immediate: true
      },
    },
    props: {
      width: {
        type: Number,
        default: window.innerWidth
      },
      height: {
        type: Number,
        default: window.innerHeight
      },
      zoom: {
        type: Number,
        default: 1
      },
      mousemoveObj: {
        type: Object,
        default: () => {}
      },
      resizeObj: {
        type: Event,
        default: () => {}
      },
      clickObj: {
        type: Object,
        default: () => {}
      },
    },
    data() {
      let scene; let camera; let renderer; let mixer; let clock; let texture; let particlesGeometry; let particlesMaterial; let particles;
      return {
        scene,
        camera,
        renderer,
        mixer,
        clock,
        texture,
        particlesGeometry,
        particlesMaterial,
        particles,
        xMove:.0001,
        yMove:.0001,
        pointlight:null,
        hideblackscreen: false,
        doAnimate: true,
        gltfscene: null,
        gltf: this.$store.state.prismicData.stage_gltf.url
      };
    },
    methods: {
      init() {
        this.scene = new THREE.Scene();
  
        this.camera = new THREE.PerspectiveCamera( 100, this.width / this.height, 0.1, 1000);
        this.camera.position.set(0,0,this.zoom);
  
        this.clock = new THREE.Clock();
  
        this.renderer = new THREE.WebGLRenderer({ alpha: false, antialias: true, powerPreference: "high-performance"});
        this.renderer.setClearColor (0x000000, 1);
        // this.renderer.setClearColor (0xffffff, 1);
        this.renderer.setPixelRatio( window.devicePixelRatio ); // prevents pixelation
        this.renderer.setSize(this.width, this.height);
        this.renderer.outputEncoding = THREE.sRGBEncoding;
        this.renderer.toneMapping = THREE.ACESFilmicToneMapping;
        this.renderer.toneMappingExposure = 1;
        this.renderer.shadowMap.enabled = true;
  
        this.$refs.model.appendChild(this.renderer.domElement);
        const controls = new OrbitControls(this.camera, this.renderer.domElement);
        controls.enableZoom = true;
        controls.enableDamping = true
      },
      lights() {
        // const light = new THREE.AmbientLight( 0x404040 ); // soft white light
        // this.scene.add( light );
        this.pointlight = new THREE.PointLight( 0xffffff, 3, 100 );
        this.pointlight.position.set( 0,2,-50);
        this.scene.add( this.pointlight );
        // const pointLightHelper = new THREE.PointLightHelper( pointlight, 1 );
        // this.scene.add( pointLightHelper );
      },
      gltfLoader() {
        const loader = new GLTFLoader();
        // let GLTFPath = require("../../public/Stage.gltf").default;
        loader.load(this.gltf, (gltf) => {
          this.gltfscene = gltf.scene;
          this.scene.add( this.gltfscene );
          this.hideblackscreen = true;
          this.animate();
        })
      },
      // loadObj() {
      //   var loader = new OBJLoader();
      //   loader.load(this.stageURL, (obj) => {
      //     // this.scene.add(obj.scene);
      //     this.scene.add(obj);
      //   })
      // },
      animate() {
        if (this.doAnimate) requestAnimationFrame(this.animate);
        else return;
        this.renderer.render(this.scene, this.camera);
        let delta = this.clock.getDelta();

        // keep zooming
        if (this.camera.position.z > -80) {
          this.camera.position.z -= 0.1;
          this.pointlight.position.y += 0.15;

          // once arrived into the darkness, go to live video
        } else {
          this.scene.remove(this.pointlight);
          this.scene.remove(this.gltfscene);
        
          
          this.doAnimate = false;
          cancelAnimationFrame(this.animate);
          if ( window.location.hash === "#/live") {
            window.location.href = "./index.html#/livevideo";
          }
          return;
          // this.$router.push("/livevideo");
        }
        
        if (this.mixer) this.mixer.update(delta);
        
      },
    },
    // emits: ["loaded"],
    mounted() {
      this.init();
      this.gltfLoader();
      // this.loadObj();
      this.lights();
      this.camera.aspect = window.innerWidth / window.innerHeight;
      this.camera.updateProjectionMatrix();
      this.renderer.setSize(window.innerWidth, window.innerHeight);
    },

  };
  </script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style lang="scss" scoped>
  canvas {
  margin: auto;
  
  }
  .stage {
    background: black;
  }
  .model {
    position: fixed;
    left: 0;
    top: 0;
    opacity: 1;
  }

  .blackscreen {
    position: fixed;
    z-index: 3;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: black;
    opacity: 1;
    transition: all 1s ease;
  }
.hide {
  opacity: 0;
  transition: all 1s ease;

}
  </style>
  